import {
	TRAER_AGENTE_A,
	TRAER_AGENTE_B,
	CARGANDO,
	ERROR
} from "../types/agentes_type";

const INITIAL_STATE = {
	agenteA: [],
	agenteB: [],
	cargando: false,
	error: ""
};

export default (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case TRAER_AGENTE_A:
			return {
				...state,
				agenteA: action.payload,
				cargando: false,
				error: ""
			};
		case TRAER_AGENTE_B:
			return {
				...state,
				agenteB: action.payload,
				cargando: false,
				error: ""
			};
		case CARGANDO:
			return {
				...state,
				cargando: true
			};
		case ERROR:
			return {
				...state,
				cargando: false,
				error: action.payload
			};
		default:
			return state;
	}
};
