import axios from "axios";

import {
	TRAER_AGENTE_A,
	TRAER_AGENTE_B,
	CARGANDO,
	ERROR
} from "../types/agentes_type";

export const traerAgente = (agenteA, agenteB) => async dispatch => {
	const respuesta = await axios.get(
		`http://registro.taxtek.co/zip_code/core.php?service=get_clients&agent_zip_code_1=85&agent_zip_code_2=30`
	);

	dispatch({
		type: TRAER_AGENTE_A,
		payload: [respuesta]
	});
	dispatch({
		type: TRAER_AGENTE_B,
		payload: [agenteB]
	});
};
